@import url(https://fonts.googleapis.com/css?family=Lato:300,400);
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Overpass+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --ecma-gold: #957E51; 
  --ecma-light-gold: rgb(163, 139, 92); 
  --ecma-dark-gold: rgb(112, 93, 54);
  --ecma-disabled-gold: rgb(199, 176, 131);
  --ecma-blue: #2F3742;
  --color-default: #413E3E;
}

body, html {
  margin: 0;
  padding: 0;
}

h1 {
  padding: 30px 0;
}

a {
  text-decoration: none;
  color: rgb(58, 135, 187);
}

a:hover {
  color: rgb(27, 98, 145);
  -webkit-transition: color 0.9s;
  transition: color 0.9s;
}

.App {
  text-align: center;
}

.section-subtitle {
  font-size: 1.3em;
  line-height: 1.6em;
}

.section-ma {
  margin-bottom: 10vh;
}

.section-container {
  padding-bottom: 60px;
}

.section-vpa {
  padding-bottom: 90px;
  padding-top: 30px;
}

.container-vpa {
  padding-bottom: 30px;
  padding-top: 60px;
}

.container-tpa {
  padding-top: 30px;
}

.container-bpa {
  padding-bottom: 60px;
}

.center {
  display: flex;
  justify-content: center;
}

.start {
  display: flex;
  justify-content: flex-start;
}

.hpa {
  padding-left: 10vw;
  padding-right: 10vw;
}

.hma {
  margin-left: 10vw;
  margin-right: 10vw;
}

@media screen and (min-width: 1366px) {
  .hma {
    margin-left: 0;
    margin-right: 0;
  }

  .hpa {
    padding-left: 0;
    padding-right: 0;
  }
}

.hm30 {
  margin-left: 30px;
  margin-right: 30px;
}

.hm-default {
  margin-left: 1em;
  margin-right: 1em;
}

.min-max-width {
  max-width: 1080px;
  min-width: 300px;
}

.roboto {
  font-family: 'Roboto', 'Haleway', serif; 
}

.lato, .text {
  font-family: 'Lato', 'Haleway', serif; 
}

.text {
  line-height: 1.8em;
  font-size: 1em;
}

.overpass-mono {
  font-family: 'Overpass Mono', monospace;
}

.color-ecma-gold {
  color: #957E51;
  color: var(--ecma-gold);
}

.color-ecma-blue {
  color: #2F3742;
  color: var(--ecma-blue);
}

.bg-ecma-blue {
  background-color: #2F3742;
  background-color: var(--ecma-blue);
}

.bg-ecma-gray {
  background-color: #EAE6E6;
}

.color-default {
  color: #413E3E;
  color: var(--color-default);
}

.color-ecma-gray {
  color: rgba(61, 86, 107, 0.6);
}

.page-min-height {
  position: relative;
  min-height: calc(100vh - 15px - 270px);
}

.button {
  display: inline-block;
  max-width: 18em;
  padding: 0.35em 1.2em;
  background-color: #957E51;
  background-color: var(--ecma-gold);
  box-sizing: border-box;
  font-weight: 400;
  font-size: 20px;
  line-height: 25px;
  text-align: center;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  margin-top: 1em;
  cursor: pointer;
  color: white;
}

.button:hover {
  background-color: rgb(163, 139, 92);
  background-color: var(--ecma-light-gold);
  color: white;
}

.button:active {
  background-color: rgb(112, 93, 54);
  background-color: var(--ecma-dark-gold);
}

.disabled, .disabled:hover, .disabled:active  {
  background-color: rgb(199, 176, 131);
  background-color: var(--ecma-disabled-gold);
  cursor: auto;
}

@media (hover: none) {
  .button:hover { background-color: #957E51; background-color: var(--ecma-gold); }
  .disabled:hover { background-color: #957E51; background-color: var(--ecma-gold); }
}

#map {
  height: 40vh;
  width: 100%;
}
nav a {
    color: var(--ecma-blue);
}

nav a:hover {
    color: var(--ecma-gold);
}

.navigation {
    padding: 0 1vw;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 1;
    width: 100%;
}

.mobile-navigation {
    box-shadow: 0 4px 2px -2px rgba( 0, 0, 0, 0.2 );
    background-color: white;
    position: fixed;
    z-index: 1;
    width: 100%;
    display: none;
}

.mobile-navigation .dropdown-content {
  margin-left: 30px;
}

.logoContainer {
    align-self: flex-start;
}

.headerLogo {
    height: 68px;
    margin-top: 7px;
}

.menuItems {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
    text-align: center;
    max-width: 75%;
}

.menu-item {
    height: 75px;
    padding: 0 20px;
    margin: 2px 0 0 0;
    border-bottom: 5px solid white;
}

.mobile-menu-item {
    display: block;
    line-height: 2.5em;
    padding: 0 25px;
    border-left: 5px solid white;
}

.menuItems p {
    font-size: 20px;
    cursor: pointer;
    color: #192835;
}

.menu-mobile {
  overflow: auto;
}

.menu-mobile a {
    font-size: 20px;
}

.menuItems p:hover, .menu-icon:hover, .menu-mobile p:hover {
    color: #957E51;
}

.menu-icon {
    display: none;
    border: none;
    background: none;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: white;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown:hover .dropdown-content, .show-dropdown {
    display: block;
}

.dropdown-content a {
    float: none;
    font-size: 16px;
    padding: 16px;
    text-decoration: none;
    display: block;
    text-align: left;
    border-left: 5px solid white;
}

.nav-active {
    border-bottom: 5px solid var(--ecma-gold);
}

.dropdown-active {
    border-left: 5px solid var(--ecma-gold) !important;
}

@media screen and (max-width: 960px) {
    .menu-item {
        display: none;
    }

    .menu-icon,  .show-mobile-menu, .show-mobile-menu .dropdown-content {
        display: block;
    }
    
    .show-mobile-menu .dropdown {
        width: 100%;
    }

    .navigation .dropdown-content {
        display: none;
    }

    .dropdown-content {
        position: relative;
        box-shadow: none;
    }
}

@media screen and (max-width: 960px) and (orientation: landscape) {
    .menu-mobile a {
        font-size: 4vh;
        line-height: 6.5vh;
    }
    .dropdown-content a {
        font-size: 3vh;
        line-height: 3.5vh;
        padding: 4px;
    }
}
.footer {
    right: 0;
    bottom: 0;
    left: 0;
}

.footer-logo-container {
    width: 35%;
}

.footer-icon {
    margin-right: 25px;
}

.footer-info {
    width: 55%;
    display: grid;
}

.footer-text {
    font-size: 1em;
    line-height: 1.4em;
}

.footer-inner {
    display: flex;
    justify-content: center;
    flex-direction: row;
    flex-grow: 1;
}

.footer-social-media {
    flex-direction: column;
    width: 15%;
}

.footer-link {
    text-decoration: none;
}

.footer-link:visited, .footer-link:link {
    color: white;
}

.footer-link:hover {
    color: lightgrey;
}

.footer-link:active {
    color: grey;
}

.social-icon {
    margin: 5px 0px 5px 0px;
}

@media screen and (max-width: 900px) {
    .footer-inner {
        flex-direction: column;
    }

    .footer-info {
        flex-direction: column;
        width: 100%;
    }

    .footer-social-media {
        flex-direction: row;
        margin-top: 30px;
        width: 100%;
    }

    .footer-logo-container {
        width: 100%;
    }

    .social-icon {
        margin: 0px 5px 0px 5px;
    }
}
form {
    min-width: 200px;
    max-width: 900px;
}

form input, form textarea, .input-mask {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 6px;
    resize: vertical;
}

form textarea {
    height: 200px;
}

.error-msg {
    color: red;
}

#formTel, .error-msg {
    margin-bottom: 16px;
}

.form-error {
    border-color: red;
}

.success-message {
    color: green;
}

.form-button {
    width: 6em;
}
.contact-container {
  background: url(/ecma-site/static/media/cover.5504993e.png);
  background-size: cover;
  color: white;
}

.contact-container a {
  color: rgb(196, 196, 196);
}

.contact-container a:hover {
  color: rgb(121, 173, 197);
}

.contact-info {
    display: grid;
    grid-template-columns: 40px auto;
    grid-template-rows: auto-fit;
    align-items: center;
    justify-content: center;
    grid-row-gap: 5px;
    grid-column-gap: 5px;
    margin-bottom: 60px;
    margin-top: 60px;
}

.grid-gap{
    padding-bottom: 10px;
}
.card-outer {
    background-color: rgb(240, 240, 240);
    margin: 20px;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
}

.card-inner {
    display: flex;
    flex-flow: row;
}

.card-container-bg {
    background-color: hsl(0, 0%, 97%);
}

.card-img {
    width: 440px;
    height: 300px;
}

.card-info {
    width: 55%;
    padding: 1em;
    flex-grow: 1;
}

.card-button {
    align-self: end;
    margin-top: auto;
}

@media screen and (max-width: 840px) {
    .card-outer {
        width: 43%;
    }

    .card-inner {
        flex-direction: column;
    }

    .card-img {
        width: 100%;
        height: 200px;
    }

    .card-info {
        width: 100%;
        height: auto;
    }

    .card-button {
        width: 100%;
    }
}

@media screen and (max-width: 700px) {
    .card-outer {
        width: 80%;
    }

    .card-img {
        height: 300px;
    }
}

@media screen and (max-width: 600px) {
    .card-outer {
        width: 100%;
    }

    .card-img {
        height: 200px;
    }
}
.service-main {
    width: 65%;
}

.service-img-side {
    margin-left: 2%;
    width: calc(60% - 20px);
}

.service-side {
    display: flex;
    flex-direction: column;
    width: 30%; 
}

.service-img-container {
    flex-direction: row;
}

.service-side div {
    margin-bottom: 20px;
    background-color: rgb(240, 240, 240);
}

.service-side .service-text {
    padding: 0;
}

.other-services p {
    font-size: 3em;
}

.service-text {
    padding: 0 20px;
}

.service-image {
    display:block;
    margin:auto auto auto 40px;
}

.other-img {
    background-position: center;
    height: 225px;
    width: 225px;
    -webkit-clip-path: polygon(0 50%, 50% 100%, 100% 50%, 50% 0);
            clip-path: polygon(0 50%, 50% 100%, 100% 50%, 50% 0);
    margin: 20px;
}

.service-panel {
    margin-top: 10px;
    flex-direction: row;
    justify-content: space-between;
}

@media screen and (max-width: 900px) {
    .service-text {
        width: 100%;
        padding: 0;
    }

    .service-image {
        width: 100%;
        margin-left: 0;
    }

    .service-side {
        width: 100%;
        margin-left: 0;
        margin-right: 0;
    }

    .service-img-side {
        margin-left: 0;
        width: 100%;
        order: -1;
    }

    .service-img-container {
        flex-direction: column;
    }

    .service-panel {
        flex-direction: column;
    }

    .service-main {
        height: auto;
        width: 100%;
    }

    .service-side div {
        margin-bottom: 0;
        background-color: white;
    }
}
.member-container {
    margin-bottom: 10px;
}

.member-photo {
    width: 50%;
    object-fit: cover;
}

.member-info {
    width: 50%;
    flex-grow: 1;
}

.member-info-1 {
    background-color: var(--ecma-blue);
    color: rgb(236, 236, 236);
    line-height: 1.5em;
    font-size: 0.9em;
}

.member-info-2 {
    font-size: 0.9em;
}

.member-info-1, .member-info-2 {
    width: 50%;
}

.member-info-1, .member-info-2, .member-info-3 {
    padding: 1.5em;
}

.member-info-3 {
    background-color: rgb(219, 219, 219);
    width: 100%;
}

.triangle-left {
    border-right: 20px solid var(--ecma-blue);
    margin-left: -20px;
}

.triangle-right {
    border-left: 20px solid var(--ecma-blue);
    margin-right: -20px;
}

.triangle-left, .triangle-right {
    width: 0; 
    height: 0;
    position: relative;
    top: 37px; 
    border-top: 15px solid transparent;
    border-bottom: 15px solid transparent;
}

.team-right {
    flex-direction: row;
}

.team-left {
    flex-direction: row-reverse;
}

@media screen and (max-width: 960px) {
    .member-container {
        flex-direction: column;
    }

    .member-photo {
        width: 100%;
        height: 50%;
        object-fit: fill;
    }

    .member-info {
        width: 100%;
        height: 50%;
        flex-direction: row;
    }

    .member-info-1, .member-info-2 {
        width: 50%;
    }

    .member-info-1 {
        order: -1;
    }

    .triangle-left, .triangle-right {
        border-left: 10px solid transparent;
        border-right: 10px solid transparent;
        border-bottom: 15px solid var(--ecma-blue);
        border-top: 0;
        margin-top: -15px;
        top: 0;
        right: 46%;
    }

    .triangle-left {
        margin-left: 0;
        margin-right: -20px;
    }

    .team-left {
        flex-direction: row;
    }
}

@media screen and (max-width: 500px) {
    .member-info-1, .member-info-2 {
        padding: 1em;
    }

    .member-info-1 {
        width: 40%;
        font-size: 0.8em;
    }

    .member-info-2 {
        font-size: 0.8em;
        width: 60%;
        padding: 1.5em 0.5em;
    }

    .triangle-left, .triangle-right {
        right: 36%;
    }
}
.clinic-panel {
    width: 50%;
    height: auto;
}

.clinic-hidden {
    display: none;
}

.clinic-history, .clinic-panel {
    padding: 1em 2em 1em 2em;
    margin-top: 1em;
    margin-bottom: 1em;
}

.clinic-history {
    background-color: rgb(240, 240, 240);
}

.history-panel {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 30px;
  margin-bottom: 30px;
}

#history-img1 {
  width: 30%;
}

#history-img2 {
