}

@media screen and (max-width: 900px) {
  .clinic-panel {
      width: 100%;
      height: auto;
  }

  .history-panel {
    margin-top: 0;
    margin-bottom:0;
  }

  #history-img1, #history-img2, #history-img3, #history-text3, #history-img4, #history-text4, #newecma-img1, #newecma-img2 {
