.icon-card {
    width: 325px;
    padding: 2em;
}

.icon-card-text {
    height: 4.5em;
    line-height: 1.5em;
}

@media screen and (max-width: 750px) {
    .icon-card {
        width: 100%;
    }
}
.carousel {
  width: 100%;
  height: auto;
  overflow: hidden;
  padding-bottom: 30px;
}

.carousel-container {
  display: flex !important;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-color: rgba(25, 40, 53, 0.61);
  width: 100%;
  height: calc(100vh - 75px);
  background-blend-mode: overlay;
  overflow: hidden;
  padding-bottom: calc(60px + 0.67em);
}

.carousel-clinic {
  background-image: url(/ecma-site/static/media/clinica.f33ab4cb.jpg);
}

.carousel-relax {
  background-image: url(/ecma-site/static/media/CarrosselEspacoRelaxar.9cd182be.jpg);
}

.carousel-pilates {
  background-image: url(/ecma-site/static/media/CarrosselPilates.1fec840a.jpg);
}

.carousel-personal {
  background-image: url(/ecma-site/static/media/CarrosselPersonal.9da23867.jpg);
  background-position: top !important;
}

.carousel-title {
  font-size: 4vh;
}

.carousel-subtitle {
  font-weight: 300;
  font-size: 4vh;
}

.slick-dots li button:before {
  font-size: 10px;
  line-height: 20px;
  color: white;
}

.slick-dots li.slick-active button:before {
  color: var(--ecma-gold);
}

@media (orientation: landscape) {
  .carousel-title {
      font-size: 7vh;

  }
  .carousel-subtitle {
      font-size: 4vh;
  }
}


